/*  БАЗА */
h1,
h2,
h3,
p,
ul {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding-left: 0;
}
/*  СТИЛИ */
.popular-recipe-h2 {
    color: #050505;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.33;
    text-transform: uppercase;
}

.popular-recipe-div {
    border: 1px solid tomato;
    width: 332px;
    margin-top: 40px;
}

.favorite-list-img{
    border: 1px solid tomato;
}

.popular-recipe-list {
    margin-top: 20px;
    height: 100px;
}

.popular-recipe-list-li {
display: flex;

}

.popular-recipe-list-li:not(:last-child) {
    margin-bottom: 24px;
}

.popular-recipe-div-list{
    border: 1px solid tomato;
    margin-left: 16px;
    width: 250px;
    overflow: hidden; 
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
}

.favorite-list-h2 {
color: #050505;
font-family: Inter;
font-size: 14px;
font-style: normal;
font-weight: 600;
line-height: 1.28;
text-transform: uppercase;
width: 250px;
}

.favorite-list-h3 {
    overflow: hidden;
    color: rgba(5, 5, 5, 0.80);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    margin-top: 8px;
    width: 250px;
    height: 30px;
}

.favorite-list-img {
    width: 64px;
    height: 64px;
}

.popular-recipe-list-li:nth-last-child(-n + 2) {
    display: none;
  }

@media screen and (min-width: 768px) {
    .popular-recipe-list-li:nth-last-child(-n + 2) {
        display: flex;
      }
    .popular-recipe-div {
        width: 176px;
        margin-top: 32px;
    }
    .favorite-list-img {
        width: 48px;
        height: 48px;
    }
    .favorite-list-h2 {
        width: 91px;
        height: 18px;
        overflow: hidden;
    }
    .favorite-list-h3 {
        overflow: hidden;
        font-size: 10px;
        line-height: 1.4;
        margin-top: 8px;
        width: 120px;
        height: 41px;
    }
    .popular-recipe-div-list{
        margin-left: 8px;
        width: 120px;
    }
    .popular-recipe-list-li:not(:last-child) {
        margin-bottom: 16px;
    }
    }
  
  @media screen and (min-width: 1280px) {
    .popular-recipe-div {
        width: 235px;
    }
    .favorite-list-img {
        width: 64px;
        height: 64px;
    }
    .favorite-list-h2 {
        width: 136px;
        height: 18px;
        overflow: hidden;
    }
    .favorite-list-h3 {
        width: 155px;
        height: 41px;
    }
    .popular-recipe-div-list{
        margin-left: 16px;
        width: 155px;
    }
    .popular-recipe-list-li:not(:last-child) {
        margin-bottom: 32px;
    }
  }